import React from "react"
import 'regenerator-runtime/runtime';
import 'react-virtualized/styles.css';
import {List} from 'react-virtualized';
import {Redirect, Route, Switch, NavLink} from "react-router-dom"

import {goFetch} from "../../../api/misc";
import Spinner from '../../widgets/Spinner';
import Detail from './stock/Detail';

class Comp extends React.Component {

  constructor(props) {
    super(props);
    this.renderRow = this.renderRow.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    ['selectStock', 'addToCollection', 'setActiveTab', 'makeChosen'].forEach(m => this[m] = this[m].bind(this));
    const stocks = localStorage.stocks ? JSON.parse(localStorage.stocks) : [];
    this.stocks = stocks;
    this.state = {
      stocks,
      tabs: ['all', 'chosen'],
      activeTab: 'all',
      search: '',
      chosen: localStorage.chosen ? JSON.parse(localStorage.chosen) : [],
    }
  }

  async componentDidMount() {
    if (!this.state.stocks.length) {
      const stocks = await (await goFetch('/stocks')).json();
      this.stocks = stocks;
      this.setState({stocks});
      localStorage.stocks = JSON.stringify(stocks);
    }
  }

  handleSearch(e) {
    const {name, value} = e.target;
    const {activeTab, chosen} = this.state;

    const search = s => s.name.includes(value) || s.symbol.includes(value);

    if (activeTab === 'all') {
      const stocks = !value ? this.stocks : this.stocks.filter(search);
      this.setState({[name]: value, stocks});
    } else if (activeTab === 'chosen') {
      const stocks = !value ? this.makeChosen(chosen) : this.makeChosen(chosen).filter(search);
      this.setState({[name]: value, stocks});
    }
  }

  makeChosen(chosen) {
    return this.stocks.filter(s => chosen.includes(s.symbol))
  }

  setActiveTab(e) {
    const {activeTab} = this.state;
    const tab = e.target.textContent;
    if (activeTab === tab) {
      return;
    }
    this._setTab(tab);
  }

  _setTab(tab) {
    const {chosen} = this.state;
    if (tab === 'all') {

      this.setState({
        search: '',
        activeTab: tab,
        stocks: this.stocks
      });
      return;
    }

    if (tab === 'chosen') {
      this.setState({
        search: '',
        activeTab: tab,
        stocks: this.makeChosen(chosen)
      });
      return;
    }
  }

  selectStock(symbol) {

    const stock = this.state.stocks.find(s => s.symbol === symbol);
    this.setState({
      stock: {...stock, chosen: this.state.chosen.includes(stock.symbol)},
    });
  }

  addToCollection(e) {
    const {symbol} = e.target.dataset;
    let {chosen, stocks, activeTab} = this.state;
    const stock = this.stocks.find(s => s.symbol === symbol);
    if (chosen.includes(symbol)) {
      // 取消收藏

      chosen = chosen.filter(v => v !== symbol);
      localStorage.chosen = JSON.stringify(chosen);

      this.setState({
        chosen,
        stocks: activeTab === 'all' ? stocks : stocks.filter(s => s.symbol !== symbol)
      });

      return;
    }
    chosen = [symbol, ...chosen];
    localStorage.chosen = JSON.stringify(chosen);
    this.setState({
      chosen,
      stocks: activeTab === 'all' ? stocks : [stock, ...stocks]
    });
  }

  renderRow({index, key, style}) {
    const {url} = this.props.match;
    const s = this.state.stocks[index];

    return <NavLink key={key} className="panel-block" activeClassName="is-active"
                    style={style} to={url + '/' + s.symbol}>
              <span className="panel-icon">
                <i className="fa fa-book"/>
              </span>
      {(index + 1) + '. '} {s.name}({s.symbol})
    </NavLink>
  }

  render() {
    const {stocks, search, tabs, activeTab, chosen} = this.state;

    return <div className='container unset-max-width'>
      <div className="columns">
        <div className="column is-3" style={{width: '294px'}}>
          <nav className="panel">
            <p className="panel-heading">
              Stocks
            </p>
            <div className="panel-block">
              <p className="control has-icons-left">
                <input className="input is-small" type="text" placeholder="search" name='search' value={search}
                       onChange={this.handleSearch}/>
                <span className="icon is-small is-left">
                  <i className="fa fa-search"/>
                </span>
              </p>
            </div>
            <p className="panel-tabs">
              {
                tabs.map(tab => {
                  return <a className={tab === activeTab ? "is-active" : void 0} key={tab}
                            onClick={this.setActiveTab}>{tab}</a>
                })
              }
            </p>
            <div className="panel-block">
              点击股票名称试试?
            </div>


            {
              (activeTab === 'chosen' || stocks.length) ? (<List style={{outline: 'none'}}
                                                                 width={270}
                                                                 height={410}
                                                                 rowCount={stocks.length}
                                                                 rowHeight={41}
                                                                 rowRenderer={this.renderRow}
              />) : <a className="panel-block" style={{justifyContent: 'space-around'}}> <Spinner name='circle'
                                                                                                  style={{marginTop: ''}}/></a>

            }
          </nav>
        </div>
        <div className="column is-9">
          <Route exact path='/d/Stock/:symbol' render={routerProps => {
            const {symbol} = routerProps.match.params;
            const stock = this.stocks.find(s => s.symbol === symbol);
            return <Detail {...routerProps} {...this.state}
                           selectStock={this.selectStock}
                           addToCollection={this.addToCollection}
                           stock={stock}
                           chosen={chosen.includes(stock.symbol)}
            />
          }}/>
        </div>
      </div>
    </div>
  }
}

export default Comp
